.wrapper {
    display: flex;
    flex-direction: column;
    font-family: "Open Sans", sans-serif;
}

.header {
    display: flex;
    flex-direction: row;
}

.main {
    flex: 1;
}

.logoContainer {
    display: flex;
    justify-content: center;
    align-items: center;
}

.headerContentContainer {
    background-color: #554590;
    width: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.assistanceContainer {
    width: 270px;
    margin-right: 2rem;
}

.assistanceTitle {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
}

.searchContainer {
    display: flex;
    align-items: center;
    position: relative;
    margin-right: 1.5rem;
}

.searchInput {
    width: 270px;
    height: 36px;
    border-radius: 18px;
    border: none;
    padding-left: 1.5rem;
    padding-right: 45px;
}

.searchLensImage {
    position: absolute;
    right: 16px;
}
